@use '../../../scss/Foundation/mixin';

.p-works__container{
    order:1;
    width:calc(920/1440*100vw);
    padding-bottom:calc(40/1440*100vw);
    @include mixin.mq(tb){
        width:calc(800/1023*100vw);
        justify-content: center;
    }
    @include mixin.mq(sp){
        width:calc(343/375*100vw);
    }
}